<template>
  <div>
    <div class="adiv">
      <van-icon name="arrow-left" class="myicon" @click="$router.back()" />
      <h1>我的发布</h1>
    </div>
    
       
        <van-empty image="error" description="暂无发布" v-show="this.mysendlist.length==0" />

    
    <van-panel
      v-for="(item, index) in mysendlist"
      :key="index"
      :title="`第${index + 1}条发布`"
      :desc="
        new Date(parseInt(item.time)).toLocaleString().replace(/:\d{1,2}$/, '')
      "
    > <router-link
          :to="{ path: '/detailed', query: { item_id:item._id,cate_id:item.cate_id} }"
          custom
          v-slot="{ navigate }"
        >
          <span @click="navigate" @keypress.enter="navigate" role="link">
      <div class="con">我的发布标题《{{ item.title }}》</div>
       </span>
        </router-link>
      <template #footer>
        <van-button size="small" type="danger" @click="del(index)"
          >删除</van-button
        >
      </template>
    </van-panel>
  </div>
</template>

<script>
import { getsendlistAPI, getdelsendAPI } from "@/api/home.js";

export default {
  name: "mysend",
  data() {
    return {
      mysendlist: [],
      loginfo: JSON.parse(sessionStorage.getItem("loginfo")),
    };
  },
  methods: {
   
    huoqu() {
      getsendlistAPI(this.loginfo.uid)
        .then((result) => {
          console.log("result: ", result);
          this.mysendlist = result.data.data;
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    del(index) {
      this.$dialog
        .confirm({
          message: "确定删除吗",
        })
        .then(() => {
 

          getdelsendAPI({
            _id:this.mysendlist[index]._id,
            uid: this.loginfo.uid,
          })
            .then((result) => {
              console.log("result: ", result);
              this.$toast("删除成功");
              this.$notify({ type: "danger", message: "删除成功" });
              this.huoqu();
            })
            .catch((err) => {
              console.log("err: ", err);
            });
        })
        .catch(() => {
          this.$toast("取消删除");
          this.$notify({ type: "success", message: "取消删除" });
        });
    },
  },
  created() {
    this.huoqu()

  },
};
</script>

<style lang="scss" scoped>

.adiv {
  width: 100vw;
  height: 50px;
  background-color: $mycolor;
  display: flex;
  align-items: center;

  color: aliceblue;
  font-size: 14px;
}
h1 {
  margin: 0 auto;
  font-size: 16px;
}
.myicon {
  font-size: 32px;
}
.con {
  font-size: 16px;
  text-indent: 19px;
}
</style>

